import React from 'react';
import styles from './festivalList.module.css';

const FestivalList = ({item, onClickDetail}) => {
    const {title, addr1, eventstartdate, eventenddate, tel, firstimage} = item;
    

    return (
            
            <li className={styles.list} onClick={() => onClickDetail(item)} >
                <p className={styles.fstvname}>{title}</p>
                <img className={styles.img} src={firstimage} alt=""/>
                <p className={styles.date}>{eventstartdate} ~ {eventenddate}</p>
                <p className={styles.area}>{addr1}</p>
                <p className={styles.tel}>{tel}</p>
            </li>
            
    );
};

export default FestivalList;