import './App.css';

import Main from './component/main';
import Festival from './data/festival'

import { useEffect, useRef, useState } from 'react';





function App() {
  const [ftv, setFtv] = useState([]);
  const [bottomDe, setBottomDe] = useState(0);
  const [clickItem, setClickItem] = useState();

  const [year, setYear] = useState('2021');
  const [mon, setMon] = useState(1);
  const [area, setArea] = useState('');
  const yearRef = useRef();
  const monRef = useRef();
  const areaRef = useRef();
  const yearChange = () => setYear(yearRef.current.value)
  const monChange = () => setMon(monRef.current.value) 
  const areaChange = (value) => setArea(value)
  const minus = (mon) => setMon(mon <= 1 ? mon = 12 : mon - 1)
  const plus = (mon) => setMon(mon >= 12 ? mon = 1 : mon + 1)
  console.log(mon)


  const detail = (item) => {
        bottomDe === 0 ? setBottomDe(bottomDe +1) : setBottomDe(0)
        
        setClickItem(item) 
        
    } 

    

    useEffect( () => {
      
    festival  
    .festivalData(year, mon, area)
    .then(ftv => setFtv(ftv)); 
      
    }
      ,[area, year, mon])

     
      const festival = new Festival() 
  return (

      
      <Main ftv={ftv} onClickDetail={detail} bottomDe={bottomDe} clickItem={clickItem} year={year} yearRef={yearRef} monRef={monRef} areaRef={areaRef} mon={mon} area={area} yearChange={yearChange} monChange={monChange} areaChange={areaChange} minus={minus} plus={plus} />
      
   
  );
}

export default App;
